.tb_r_view{
    padding: 3px 5px;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, .5);
    color: #fff;
    font-size: 12px;
    position: absolute;
    z-index: 3;
    display: flex;
    font-weight: 400;
    align-items: center;
    min-width: 40px;
    left: var(--tb-icon-position);
    top: var(--tb-icon-position);
    line-height: initial;

}

.tb_r_view .tb__icon{
    margin-right: 3px;
    font-weight: 600;
}